		},
		applyBrand() {
			let json = {
				...this.tissue,
				id: parseInt(this.brandId),
			}
			this.$util.post("/applicationToJoin/set", JSON.stringify(json)).then(res => {
				this.$util.success(res.data.msg)
				this.middle = false
			})
		},
		solicit() {
			if (this.lively) return
			this.lively = true
			let time = 60
			this.getCode = time + "s"
			this.time = setInterval(() => {
					if (time == 0) {
						clearInterval(this.time)
						this.lively = false
						this.getCode = "获取验证码"
						this.time = null
					} else {
						time--
						this.getCode = time + "s"
					}
				},
				1000)

			//获取验证码
			this.$util.post("/login/getSms", {
				phone: this.tissue.phone
			}).then(res => {
				if (res.data.status == 200) {
					uni.showToast({
						title: '发送成功',
					})
				} else this.$util.fail(res.data.msg)
			})
		},
